// import { Link } from "gatsby"
// import PropTypes from "prop-types"
import React, { useEffect } from "react"

import {Container, Title, VerticalPadding, TitleContainer, DefaultSizes} from '../../styles/globalStyles';
import { ItemsFlex } from '../../styles/portfolioStyles'
// import {text} from '../../constants'
import PortfolioItem from "./portfolioItem";


import websitesAppsImage from '../../assets/images/illustrations/webdev.svg'
import uiuxImage from '../../assets/images/illustrations/uiux.svg'
import graphicDesignImage from '../../assets/images/illustrations/graphicdesign.svg'
import searchImage from '../../assets/images/illustrations/search-engine.svg'
import marketing from '../../assets/images/illustrations/diagram.svg'


//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'

import firebase from "gatsby-plugin-firebase"

const cardDescriptions = [
    {   id: 0, 
        title: "Websites e apps.", 
        desc: "Quer mostrar o potêncial da sua empresa ou criar um portfólio pessoal? Faremos as suas ideias, uma realidade. Desenhamos e criamos websites e apps adaptáveis a qualquer tipo de dispositivos.",
        image: websitesAppsImage
    },
    {   id: 1, 
        title: "Design Gráfico.", 
        desc: "Acreditamos que ter uma identidade forte e reconhecível é a chave para um negócio com sucesso. Assim, ajudamos pessoas e empresas a tomar decisões corretas, um passo de cada vez.",
        image: graphicDesignImage
    },
    {   id: 2, 
        title: "UI/UX Design", 
        desc: "Encontramos o melhor design para o vosso website ou aplicação móvel. A interção com o utilizador é sempre prioridade, e é com esse pensamento que desenhamos todos os nossos projetos.",
        image: uiuxImage
    },
    {   id: 3, 
        title: "SEO", 
        desc: "Potencializamos e melhoraramos o posicionamento de seu website nas páginas de resultados dos motores de busca.",
        image: searchImage
    },
    {   id: 4, 
        title: "Marketing Digital", 
        desc: "Colocamos o seu projeto num melhor patamar digital!",
        image: marketing
    }

]



const Portfolio = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    // Firebase
  const [services, setServices] = React.useState([])

  React.useEffect(() => {
    const fetchData = async () => {
      const db = firebase
        .firestore()
        .collection("services")
        .orderBy('id')
        .onSnapshot(snap => 
            setServices(snap.docs.map(doc => doc.data()))
        )
      
    }
    fetchData()
  }, [])

    return(
            <Container >
                    <ItemsFlex >

                        {services.map(item => (
                            <div>
                                <PortfolioItem reverse={item.id%2!==0} image={item.image} title={item.name} description={item.description}/>
                            </div>
                        ))}
                    </ItemsFlex>
                    <div style={{marginBottom: '100px'}}></div>
            </Container>
    )
}

export default Portfolio